import React from 'react';
import APIClient from './APIClient'
import Storage from './Storage'

import './App.css';
import Panel from './Panel.js'
import LoadingAnimation from './Decorations/LoadingAnimation.js'

class App extends React.Component {
    constructor(props) {
        super(props)

        this.state = this.initState()
    }

    initState() {
        return {
            loaded: false
        }
    }

    componentDidMount() {
        this.initFetch()
    }

    initFetch() {
        const content = Storage.LoadData()
        const session = APIClient.CreateUserSession()

        Promise.all([content, session]).then(values => {
            this.setState({
                loaded: true
            })
        })
    }

    loaded() {
        return this.state.loaded
    }

    render() {
        if (!this.loaded()) {
            return <LoadingAnimation/>
        }

        return <Panel/>
    }
}

export default App;
