const baseUrl = "https://r4xp4nhmja.execute-api.eu-west-2.amazonaws.com/default";

const DoLogs = true
const OfflineMode = false

function Log(...args) {
    if (DoLogs) {
        console.log(...args)
    }
}

const Fetch = function(url, init) {
    let headers

    if (init) {
        if (init.headers) {
            headers = init.headers
        } else {
            headers = {}
        }
    } else {
        headers = {}
    }

    return fetch(`${baseUrl}${url}`, { ...init, headers })
}

const APIRequestBatch = async function(message) {
    Log(message)

    const http_response = await Fetch("", {
        method: "POST",
        headers: {
            "Content-Type": "Application/json"
        },
        body: JSON.stringify(message)
    })

    if (!http_response.ok) {
        console.error(http_response)
        throw Error("APIRequestBatch failed")
    }

    const json_response = await http_response.json()

    Log("Response:", json_response)

    const { result, status } = json_response

    if (status !== "ok") {
        console.error(result)
        throw Error("API returned status fail")
    }

    return result
}

const APIRequest = async function(method, id, params) {
    let is_batch_request = "false"
    let message = JSON.stringify({ 
        method, id, params, is_batch_request
    })

    const http_response = await Fetch("", {
        method: "POST",
        headers: {
            "Content-Type": "Application/json"
        },
        body: message
    })

    Log("APIReqeust (single) message:")
    Log(message)

    if (!http_response.ok) {
        throw Error("APIRequest: Couldn't get '" + method + "' response")
    }

    const json_response = await http_response.json()

    Log("Response:", json_response)

    const { result, status } = json_response

    if (status !== "ok") {
        throw Error("APIRequest: '" + method + "' request failed")
    }

    return result
}

const APIClient = (function() {
    var sessionId = undefined

    return {
        EditCharacter: function(name, desc, line) {
            let params = {description: desc, name: name, starting_line: line}
            return { method: "edit_character" , params: params }
        },

        EditPlayer: function(name, desc) {
            let params = {description: desc, name: name}
            return { method: "edit_user", params: params }
        },  

        ChangeMood: function(new_mood) {
            let params = {mood: new_mood}
            return { method: "change_mood", params: params }
        },

        TriggerEvent: function(event_desc, attitude) {
            let params = {description: event_desc, attitude: attitude}
            return { method: "trigger_event", params: params }
        },

        RequestReply: function(user_message) {
            let params = {user_message: user_message}
            return { method: "request_reply", params: params }
        },

        ResetDeferred: function() {
            let params = {}
            return { method: "reset_event", params: params }
        },

        Reset: function(id) {
            let message = {
                id: id,
                is_batch_request: "true",
                requests: [
                    {
                        method: "reset_event",
                        params: []
                    }
                ]
            }

            return APIRequestBatch(message)
        },

        SendRequests: function(id, requests) {
            let message = {
                id: id,
                is_batch_request: "true",
                requests: requests
            }

            return APIRequestBatch(message)
        },

        CreateUserSession: async function() {
            if (OfflineMode) {
                sessionId = 1
                return
            }

            let params = {}
            let result = await APIRequest("create_user_session", params)

            if (!result) {
                throw Error("Loading session failed!")
            }

            sessionId = result
        },

        GetUserSession: function() {
            const id = sessionId

            if (!id) {
                throw Error("Has no session")
            }

            return id
        }
    }
})()

export default APIClient