import React from 'react';
import APIClient from './APIClient.js'
import Storage from './Storage.js'

import './Panel.css';
import './Figma.css';
import './index.css';

const defaultName = "John"

class App extends React.Component {
  constructor(props) {
    super(props)

    this.bindHandlers()
    this.state = this.getInitialStateWithChar("amelie")
    this.api_lock = false

    this.descriptionLock = false
    this.messagesEndRef = React.createRef()
  }

  bindHandlers() {
    this.changeCharacterHandler = this.changeCharacterHandler.bind(this)
    this.changeMoodHandler = this.changeMoodHandler.bind(this)
    this.triggerPresetEvent = this.triggerPresetEvent.bind(this)
    this.textInputHandler = this.textInputHandler.bind(this)
    this.triggerCustomEvent = this.triggerCustomEvent.bind(this)
    this.triggerEventHandler = this.triggerEventHandler.bind(this)
    this.sendHandler = this.sendHandler.bind(this)
    this.resetHandler = this.resetHandler.bind(this)
    this.flowersEventHandler = this.flowersEventHandler.bind(this)
    this.carCrashEventHandler = this.carCrashEventHandler.bind(this)
    this.disrespectEventHandler = this.disrespectEventHandler.bind(this)
    this.changeCharacter = this.changeCharacter.bind(this)
    this.entryToBubble = this.entryToBubble.bind(this)
    this.handleEnterKey = this.handleEnterKey.bind(this)
  }

  handleEnterKey(event) {
    if (event.key === "Enter") {
      this.sendHandler(event)
    }
  }

  scrollToBottom() {
    this.messagesEndRef.current.scrollIntoView({ behavior: 'smooth' })
  }

  componentDidUpdate() {
    this.scrollToBottom()
  }

  componentDidMount() {
    this.scrollToBottom()
  }

  getInitialState() {
    return {
      id: APIClient.GetUserSession(),
      CharDesc: "",
      CharName: "",
      CharStartLine: "",
      CharMood: "neutral",
      PlayerName: defaultName,
      PlayerDesc: "",
      ChatInput: "",
      EventDescInput: "",
      EventReaction: "none",
      ChatHistory: [],
      CharChanged: true,
      PlayerChanged: true,
      MoodChanged: false,
      NeedReset: false
    }
  }

  getInitialStateWithChar(name) {
    this.descriptionLock = false
    let npc = Storage.GetNPC(name)

    return {
      id: APIClient.GetUserSession(),
      CharDesc: npc.description,
      CharName: npc.name,
      CharStartLine: npc.line,
      CharMood: "neutral",
      PlayerName: defaultName,
      PlayerDesc: "",
      ChatInput: "",
      EventDescInput: "",
      EventReaction: "none",
      ChatHistory: [],
      CharChanged: true,
      PlayerChanged: true,
      MoodChanged: false,
      NeedReset: false
    }
  }

  notifySavedChanges() {
    this.setState({
      CharChanged: false,
      PlayerChanged: false,
      MoodChanged: false,
      NeedReset: false
    })
  }

  emptyCharacteristics() {
    return this.state.CharName === "" || this.state.PlayerName === "" || this.state.CharDesc === "" || this.state.ChatInput === ""
  }

  setCharByName(name) {
    this.descriptionLock = false
    let data = Storage.GetNPC(name)
    this.setChar(data.name, data.description, data.line)
  }

  setChar(name, desc, line) {
    this.setState(prev_state => {      
      return {
        CharDesc: desc,
        CharName: name,
        CharStartLine: line,
        ChatChrono: desc + "\n" + name + ": \"" +  line + "\"",
        ChatHistory: [],
        CharChanged: true,
        NeedReset: true,
        CharMood: "neutral"
      }
    })
  }

  setBlankCharacter(event) {
    this.setState(this.getInitialState())
  }

  changeCharacterHandler = (event) => {
    this.changeCharacter(event.target.id, event)
  }

  changeCharacter = (char, event) => {
    event.preventDefault()
    var x = char
    switch (x) {
      case 'AmelieButton':
        this.setCharByName("amelie")
        break
      case 'LandaButton':
        this.setCharByName("landa")
        break
      case 'CourtneyButton':
        this.setCharByName("courtney")
        break
      case 'GollumButton':
        this.setCharByName("gollum")
        break;
      case 'TrevorButton':
        this.setCharByName("trevor")
        break
      case 'CustomButton':
        this.setBlankCharacter()
        break
      default: 
        console.error("Error in changeCharacterHandler")
    }
  }

  changeMoodHandler(new_mood, event) {
    event.preventDefault()

    console.log(new_mood);

    this.setState({
      CharMood: new_mood,
      MoodChanged: true
    })
  }

  async triggerPresetEvent(x) {
    console.log(x)

    let preset_events = {
      FlowersEvent: {
        desc: this.state.PlayerName + " just gave flowers to " + this.state.CharName,
        attitude: "pleased"
      },

      CarCrashEvent: {
        desc: "A car had just crashed near " + this.state.CharName + "!",
        attitude: "scared"
      },

      DisrespectEvent: {
        desc: this.state.PlayerName + " had shown disrespect towards " + this.state.CharName,
        attitude: "angry"
      }
    }

    const {desc, attitude} = preset_events[x]

    await this.triggerEvent(desc, attitude)
  }

  flowersEventHandler(event) {
    event.preventDefault()
    this.triggerPresetEvent("FlowersEvent")
  }

  disrespectEventHandler(event) {
    event.preventDefault()
    this.triggerPresetEvent("DisrespectEvent")
  }

  carCrashEventHandler(event) {
    event.preventDefault()
    this.triggerPresetEvent("CarCrashEvent")
  }

  textInputHandler = event => {
    event.preventDefault()
    var x = event.target.id
    var v = event.target.value
    switch (x) {
      case 'CharName':
      case 'CharStartLine':
      case 'CharDesc':
        this.setState({
          CharChanged: true
        })
        break
      case 'PlayerName':
      case 'PlayerDesc':
        this.setState({
          PlayerChanged: true
        })
        break
      case 'EventDescInput':
      case 'ChatInput':
        break
      default:
        console.error("Error in textInputHandler")
        return
    }

    console.log(x, v)

    this.setState({[x]: v})
  }

  changeEventReaction = e => {
    e.preventDefault()
    var v = e.target.value
    console.log(v)
    this.setState({EventReaction: v})
  }

  async triggerCustomEvent(event) {
    event.preventDefault()

    if (this.state.EventReaction === "none") {
      console.error("No reaction set")
      return
    }

    console.log("custom event")

    await this.triggerEvent(this.state.EventDescInput, this.state.EventReaction)

    this.setState({
      EventDescInput: "",
      EventReaction: "none",
    })
  }

  displayWithNewlines(text) {
    return text.split("\n").map((i,key) => {
      return <div key={key}>{i}</div>;
    })
  }

  clearChatInput() {
    this.setState({ ChatInput: "" })
  }

  renderChatEntry(entry) {
    var result = ""

    if (entry.type === "notification") {
      result = entry.text + "\n"
    } else if (entry.type === "message") {
      var from = ""
      if (entry.from === "user") {
        from = this.state.PlayerName
      } else if (entry.from === "char") {
        from = this.state.CharName
      } else {
        console.error("Couldn't resolve line source: ")
        console.error(entry)
        console.error("-----------------------------")
      }
      result = from + ": \"" + entry.text + "\".\n"
    } else {
      console.error("Wrong entry type")
    }

    return result
  }

  createChatEntry(text, type, from) {
    this.setState((prevState) => {
      const new_history = prevState.ChatHistory.concat([{
        text: text,
        type: type,
        from: from
      }])

      return { ChatHistory: new_history }
    })
  }

  parseReply(reply, from) {
    this.createChatEntry(reply, "message", from)
  }

  parseEvent(desc, attitude) {
    this.createChatEntry(desc, "notification", "char")
  }

  async triggerEventHandler(event) {
    event.preventDefault()

    const reply = await this.triggerEvent(this.state.EventDescInput, this.state.EventReaction)
    this.parseReply(reply)
  }

  async triggerEvent(desc, attitude) {
    console.log("Triggering event")

    this.parseEvent(desc, attitude)

    let requests = this.createPreliminaryRequests()
    requests.push(APIClient.TriggerEvent(desc, attitude))

    let reply = await APIClient.SendRequests(this.state.id, requests)

    this.parseReply(reply, "char")
    this.notifySavedChanges()
  }

  async GetReplyFromCharacter(text, name) {
    try {
      let reply = await APIClient.GetReplyFromCharacter(this.state.id, text, name)
      return reply
    } catch (e) {
      console.error(e)
    }

    return null
  }

  getPlayerMessage() {
    return this.state.ChatInput
  }

  notifyNewMood(new_mood) {
    this.setState((prevState) => {
      const new_history = prevState.ChatHistory.concat([{
        text: prevState.CharName + "'s mood changed to " + new_mood,
        type: "notification"
      }])

      return { 
        CharMood: new_mood,
        ChatHistory: new_history 
      }
    })
  }

  async resetHandler(event) {
    console.log("Resetting...")
    try {
      await APIClient.Reset(this.state.id)

      this.setState({
        ChatHistory: [],
        CharMood: "neutral"
      })
    } catch (e) {
      console.error("changeMood: error in APIClient.Reset: ", e)
    }
  }

  createPreliminaryRequests() {
    let messages = []

    if (this.state.NeedReset) {
      messages.push(APIClient.ResetDeferred())
    }

    if (this.state.CharChanged) {
      messages.push(APIClient.EditCharacter(this.state.CharName, this.state.CharDesc, this.state.CharStartLine))
    }

    if (this.state.PlayerChanged) {
      messages.push(APIClient.EditPlayer(this.state.PlayerName, this.state.PlayerDesc))
    }

    if (this.state.MoodChanged) {
      messages.push(APIClient.ChangeMood(this.state.CharMood))
    }

    return messages
  }

  isActiveMood(mood) {
    if (mood === this.state.CharMood) {
      return {border: "3px solid rgba(162,227,196,1)"}
    } else {
      return {border: "3px solid black"}
    }
  }

  async sendHandler(event) {
    event.preventDefault()

    if (this.emptyCharacteristics()) {
      console.error("Empty characteristics, couldn't send messages")
      return
    }

    let messages = this.createPreliminaryRequests()

    const text = this.getPlayerMessage()
    let message = text
    this.clearChatInput()
    this.parseReply(message, "user")

    messages.push(APIClient.RequestReply(text))

    let reply = await APIClient.SendRequests(this.state.id, messages)

    if (!reply) {
      console.error("Failed to get reply")
      return
    }

    // NPC reply
    this.parseReply(reply, "char")

    this.api_lock = false
    this.descriptionLock = true

    this.notifySavedChanges()
  }

  chatHistory() {
    return this.state.CharDesc + "\n" + this.state.PlayerDesc + "\n" + this.state.CharName + ": " + this.state.CharStartLine + ".\n" + this.state.ChatHistory.map(elem => { return this.renderChatEntry(elem) }).join("\n")
  }

  renderChatHistory() {
    if (this.state.CharDesc) {
      return this.displayWithNewlines(this.chatHistory())
    }
    return ""
  }

  renderOld() {
    <table id="demo-window"><tbody>
        {/* Character selections */}
        <tr>
          <td colSpan="3" className="large-cell">
            <div className="menu-spacer">
              <h2>Select NPC character</h2>
              <div>
                <button id="TrevorButton" onClick={this.changeCharacterHandler}>Trevor</button>
                <button id="LandaButton" onClick={this.changeCharacterHandler}>Hans Landa</button>
                <button id="CourtneyButton" onClick={this.changeCharacterHandler}>Courtney Williams</button>
                <button id="CustomButton" onClick={this.changeCharacterHandler}>Custom...</button>
              </div>
            </div>
          </td>
        </tr>

        {/* Events & mood */}
        <tr>
          {/* Mood */}
          <td className="large-cell">
            <div className="menu-spacer">
              <h2>Moods</h2>
              <table><tbody>
                <tr><td><button id="NeutralMoodButton" onClick={this.changeMoodHandler}>Neutral</button></td></tr>
                <tr><td><button id="JoyMoodButton" onClick={this.changeMoodHandler}>Joy</button></td></tr>
                <tr><td><button id="EnvyMoodButton" onClick={this.changeMoodHandler}>Envy</button></td></tr>
                <tr><td><button id="FearMoodButton" onClick={this.changeMoodHandler}>Fear</button></td></tr>
              </tbody></table>
            </div>
          </td>

          {/* Chat */}
          <td className="large-cell">
            <div className="menu-spacer">
              <h2>Chat</h2>
              <table className="chat-window"><tbody>
                {/* Chat window */}
                <tr>
                  <td colSpan="3"><div className="dialogue-window">
                    {this.renderChatHistory()}
                  </div></td>
                </tr>
                <tr>
                  <td colSpan="2"><input id="ChatInput" onChange={this.textInputHandler} value={this.state.ChatInput} placeholder="Type here"></input></td>
                  <td><button onClick={this.sendHandler}>Reply</button></td>
                </tr>
              </tbody></table>
            </div>
          </td>

          {/* Events */}
          <td className="large-cell">
            <div className="menu-spacer"><table><tbody>
              <tr>
                <td colSpan="3"><h2>Trigger game event</h2></td>
              </tr>
              <tr>
                <td colSpan="2" rowSpan="2"><textarea id="EventDescInput" onChange={this.textInputHandler} value={this.state.EventDescInput}></textarea></td>
                <td><select onChange={this.changeEventReaction} value={this.state.EventReaction}>
                  <option value="none">It made them:</option>
                  <option value="angry">Angry</option>
                  <option value="pleased">Pleased</option>
                  <option value="scared">Scared</option>
                  <option value="neutral">Neutral</option>
                </select></td>
              </tr>
              <tr>
                <td><button onClick={this.triggerCustomEvent}>Go</button></td>
              </tr>
              <tr>
                <td colSpan="3"><h2>Preset events</h2></td>
              </tr>
              <tr>
                <td><button id="FlowersEvent" onClick={this.presetEventHandler}>Receives flowers</button></td>
                <td><button id="DisrespectEvent" onClick={this.presetEventHandler}>Show disrespect</button></td>
                <td><button id="CarCrashEvent" onClick={this.presetEventHandler}>The car crashes nearby</button></td>
              </tr>
            </tbody></table></div>
          </td>
        </tr>

        {/* Describe character | description */}
        <tr>
            <td colSpan="3" className="large-cell">
              <div className="menu-spacer">
                <h2>Describe character</h2>
                <table><tbody>
                  <tr>
                    <td>
                      <div>Name</div>
                      <input id="CharName" onChange={this.textInputHandler} value={this.state['CharName']} placeholder="Name..."></input>
                    </td>
                    <td rowSpan="2">
                      <div>Description</div>
                      <textarea id="CharDesc" onChange={this.textInputHandler} value={this.state.CharDesc}></textarea>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <div>Starting line</div>
                      <textarea id="CharStartLine" onChange={this.textInputHandler} value={this.state.CharStartLine} placeholder="Type starting line here..."></textarea>
                    </td>
                  </tr>
                </tbody></table>
              </div>
            </td>
        </tr>

        {/* Player */}
        <tr>
          <td colSpan="3" className="large-cell">
            <div className="menu-spacer">
              <h2>Player's description</h2>

              <h3>Name</h3>
              <div className="menu-input">
                <div><input id="PlayerName" onChange={this.textInputHandler} value={this.state.PlayerName} placeholder="Your name..."></input></div>
              </div>

              <h3>Description (optional)</h3>
              <div className="menu-input">
                <div><textarea id="PlayerDesc" onChange={this.textInputHandler} value={this.state.PlayerDesc}></textarea></div>
              </div>
            </div>
          </td>
        </tr>
      </tbody></table>
  }

  entryToBubble(e) {
    if (e.type === "message") {
      if (e.from === "char") {
        return (
          <div className="chat-bubble-container left-float">
            <div className="chat-bubble">
              {e.text}
            </div>
            <br/>
            <div className="bubble-annot left-float">{this.state.CharName}</div>
          </div>
        )
      } else if (e.from === "user") {
        return (
          <div className="chat-bubble-container right-float">
            <div className="chat-bubble">
              {e.text}
            </div>
            <br/>
            <div className="bubble-annot right-float">{this.state.PlayerName}</div>
          </div>
        )
      } else {
        console.error("wrong message source")
      }
    } else if (e.type === "notification") {
      return (
        <div>
          <div className="narrative-block left-float">
            {e.text}
          </div>
        </div>
      )
    } else {
      console.error("wrong message type")
    }
  }

  startingLineBubble() {
    if (this.state.CharStartLine) {
      return (
        <div className="chat-bubble-container left-float">
          <div className="chat-bubble">
            {this.state.CharStartLine}
          </div>
          <br/>
          <div className="bubble-annot left-float">{this.state.CharName}</div>
        </div>
      )
    }
  }

  dialogueBubbles() {
    if (this.state.CharDesc) {
      return (
        <div>
          <div className="narrative-block">
            {this.state.CharDesc}
          </div>

          <br/>

          {this.startingLineBubble()}

          {this.state.ChatHistory.map(this.entryToBubble)}
        </div>
      )
    }
    return ""
  }

  render() {
    return (
      <div className="v8_10">
        <div className="v8_1">
          <div className="v6_67"></div>
          <button className="v23_24" onClick={this.triggerCustomEvent}>
            <span className="v23_26">Go</span>
          </button>
          <select className="v23_27" onChange={this.changeEventReaction} value={this.state.EventReaction}>
            <option value="none">It made them:</option>
            <option value="angry">Angry</option>
            <option value="pleased">Pleased</option>
            <option value="scared">Scared</option>
            <option value="neutral">Neutral</option>
          </select>
          <button className="v23_2" onClick={this.carCrashEventHandler}>
            <span className="v23_4">Car crashes nearby</span>
          </button>
          <button className="v23_5" onClick={this.flowersEventHandler}>
            <span className="v23_7">Receives flowers</span>
          </button>
          <button className="v23_19" onClick={this.disrespectEventHandler}>
            <span className="v23_21">Show disrespect</span>
          </button>
          <span className="v6_68">Events</span>
          <span className="v6_71">Preset events</span>
          <span className="v6_72">Trigger custom event</span>
          <textarea className="v23_31" placeholder="Describe..." id="EventDescInput" value={this.state.EventDescInput} onChange={this.textInputHandler}>
          </textarea>
        </div>
        <div className="v8_2">
          <div className="v6_69"></div>
          <span className="v23_45">Starting line</span>
          <span className="v23_44">Description</span>
          <textarea className="v23_35" placeholder="Describe..." id="CharDesc" value={this.state.CharDesc} onChange={this.textInputHandler}>
          </textarea>
          <span className="v23_49">Description</span>
          <textarea className="v23_50" placeholder="Describe..." id="PlayerDesc" value={this.state.PlayerDesc} onChange={this.textInputHandler}>
          </textarea>
          <input className="v23_41" placeholder="Name" id="CharName" value={this.state.CharName} onChange={this.textInputHandler}>
          </input>
          <input className="v23_46" placeholder="Name" id="PlayerName" value={this.state.PlayerName} onChange={this.textInputHandler}>
          </input>
          <textarea className="v23_38" placeholder="Type here..." id="CharStartLine" value={this.state.CharStartLine} onChange={this.textInputHandler}>
          </textarea>
          <span className="v6_73">NPC</span>
          <span className="v6_74">Player</span>
          <span className="v6_70">Characteristics</span>
        </div>
        <div className="v8_9">
          <div className="v8_7">
            <span className="v6_54">Change the mood here</span>
            <button className="v6_55" id="EnvyMoodButton" onClick={e => this.changeMoodHandler("envy", e)} style={this.isActiveMood("envy")}>
              <span className="v6_57">Envy</span>
            </button>
            <button className="v6_58" id="JoyMoodButton" onClick={e => this.changeMoodHandler("joy", e)} style={this.isActiveMood("joy")}>
              <span className="v6_60">Joy</span>
            </button>
            <button className="v6_61" id="NeutralMoodButton" onClick={e => this.changeMoodHandler("neutral", e)} style={this.isActiveMood("neutral")}>
              <span className="v6_63">Neutral</span>
            </button>
          </div>
          <button className="v6_64" id="FearMoodButton" onClick={e => this.changeMoodHandler("fear", e)} style={this.isActiveMood("fear")}>
            <span className="v6_66">Fear</span>
          </button>
        </div>
        <div className="v8_8">
          <div className="v6_30"></div>
          <div className="v23_54">
            <span className="v6_31">Select NPC</span>
            <button className="v6_47" id="GollumButton" onClick={e => this.changeCharacter("GollumButton", e)}>
              <span className="v6_38">Gollum</span>
            </button>
            <button className="v6_46" id="CourtneyButton" onClick={e => this.changeCharacter("CourtneyButton", e)}>
              <span className="v6_40">Courtney</span>
            </button>
            <button className="v6_52" id="AmelieButton" onClick={e => this.changeCharacter("AmelieButton", e)}>
              <span className="v6_39">Amelie</span>
            </button>
            <button className="v6_48" id="TrevorButton" onClick={e => this.changeCharacter("TrevorButton", e)}>
              <span className="v6_39">Trevor</span>
            </button>
            <button className="v6_49" id="CustomButton" onClick={e => this.changeCharacter("CustomButton", e)}>
              <span className="v6_51">Custom...</span>
            </button>
          </div>
        </div>
        <div className="v8_6">
          {/*
          <div className="narrative-block left-float">
            Courtney is a hot bitchy girl. She has huge tits and round ass. She likes money and sex. Here is her direct messages with You
          </div>

          <div className="chat-bubble-container right-float">
            <div className="chat-bubble">
              Hi there sweetie
            </div>
            <br/>
            <div className="bubble-annot right-float">YOU</div>
          </div>

          <div className="chat-bubble-container left-float">
            <div className="chat-bubble">
              CHUMBA
            </div>
            <br/>
            <div className="bubble-annot left-float">Courtney</div>
          </div>
          */}

          <div className="dialogue-container">
            {this.dialogueBubbles()}
            <div className="left-float hidden-anchor" ref={this.messagesEndRef}></div>
          </div>

          <textarea className="v8_5" placeholder="Type here" id="ChatInput" value={this.state.ChatInput} onChange={this.textInputHandler} onKeyPress={this.handleEnterKey}>
          </textarea>
          <button className="v8_4" onClick={this.resetHandler}>
            <span className="v6_41">RESET</span>
          </button>
          <button className="v8_3" onClick={this.sendHandler}>
            <span className="v6_42">REPLY</span>
          </button>
        </div>
      </div>
    )
  }
}

export default App;
