const Storage = (function() {
    var loadSuccess = false
    const npcPath = "npcs.json"
    const doLogs = false

    function Log(...args) {
        if (doLogs) {
            console.log(...args)
        }
    }

    let npcList = null
    let npcData = null

    function AssertLoaded() {
        if (!loadSuccess) {
            throw Error("Not loaded!")
        }
    }

    return {
        LoadData: function() {
            if (loadSuccess) {
                return
            }

            const promise = fetch(npcPath, {
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                }
            }).then(resp => {
                return resp.json()
            }).then((res) => { 
                Log("NPC file")
                Log(res)

                npcList = res["list"]
                npcData = res["npcs"]

                loadSuccess = true
            })
    
            return promise
        },

        NPCList: function() {
            AssertLoaded()
            const result = npcList

            if (!result) {
                throw Error("Couldn't load character list")
            }

            return result
        },

        GetNPC: function(name) {
            AssertLoaded()
            const result = npcData[name]

            if (!result) {
                throw Error("Couldn't find character '" + name + "'")
            }
            return result
        }
    }
})()

export default Storage